<template>
  <div class="auth-template">
    <div v-if="alert">
      <v-alert
        class="success alert"
        type="success"
      >
        <span v-html="alert"></span>
      </v-alert>

      <v-btn
        class="btn-voltar primaryDark py-4 px-6"
        dark
        height="auto"
        @click="goTo('home')"
      >
        Voltar para o site
      </v-btn>
    </div>

    <v-card
      v-else
      dark
      class="primaryDark card"
    >
      <v-card-title class="title pa-0 mb-4">
        {{ title }}
      </v-card-title>
      <slot></slot>
    </v-card>
  </div>
</template>

<script>
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  mixins: [ NavigationMixin ],
  props: {
    title: { required: true },
    alert: { default: null }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/theme.scss';

.auth-template {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 130px); 
}
.alert { max-width: 100%; width: 320px; margin-top: 30px; }
.btn-voltar { margin: 15px auto 0; display: block; }
.card { max-width: 100%; width: 320px; padding: 20px; border-top: 3px solid $secondary !important; }
</style>